<template>
    <div style="padding-top: 2px;">
        <el-tabs type="border-card" tab-position="left" height="800px" @tab-click="tab_click">
            <el-tab-pane v-for="project_type in project_type_code_list" :key="project_type.codeValue" :label="project_type.codeValue">
            </el-tab-pane>
            <el-row :span="3" style="height:100px;">
                <el-form :inline="true" ref="searchFormRef" :model="searchFormData" size="mini">
                    <!--<el-form-item label="项目" prop="project">-->
                        <!--<el-input v-model="searchFormData.project" placeholder="请输入项目"></el-input>-->
                    <!--</el-form-item>-->
                    <el-form-item label="项目名称" prop="project_name">
                        <el-input v-model="searchFormData.project_name" placeholder="请输入项目名称"></el-input>
                    </el-form-item>
                    <el-form-item label="项目上下文" prop="project_context">
                        <el-input v-model="searchFormData.project_context" placeholder="请输入项目上下文"></el-input>
                    </el-form-item>
                    <div class="v-search-opt">
                        <div class="v-pagination">
                            <el-pagination
                                    @size-change="handleSizeChange"
                                    @current-change="handleCurrentChange"
                                    :current-page="searchFormData.pageNum"
                                    :page-size="searchFormData.pageSize"
                                    :total="searchFormData.pageCount"
                                    layout="total, prev, pager, next, jumper"
                            >
                            </el-pagination>
                        </div>
                        <div class="v-inline">
                            <el-button size="mini" type="primary" @click="onSearch"><i class="iconfont icon-chaxun"></i> 查询</el-button>
                            <el-button size="mini" type="primary" @click="onReset"><i class="iconfont icon-chongzhi"></i> 重置</el-button>
                        </div>
                    </div>
                </el-form>
                <hr>
            </el-row>
            <el-row>
                <el-col :span="3" v-for="data in project_data" :key="data._id">
                    <div class="project-content">
                        <router-link :to="{name:'projectServiceContainerConf',params:{project_id:data._id}}">
                            <div class="project-info">
                                <i class="iconfont icon-xiangmutubiao set-ico"></i>
                                <h4 style="height:10px">{{data.project_name}}</h4>
                                <h4 style="height:10px">{{data.project_context}}</h4>
                            </div>
                        </router-link>
                    </div>
                </el-col>
            </el-row>
        </el-tabs>
    </div>
</template>

<script>
    export default {
        name: 'projectClassManage',
        data() {
            return {
                project_type_code_list: null,
                searchFormData: {
                    pageNum: 1,
                    pageSize: 32,
                    pageCount: 1,
                    order: '+project_context'
                },
                selected_project_type:'',
                project_data: [],
            }
        },
        async mounted() {
            this.project_type_code_list = await this.$setCodeList(this.project_type_code_list, 'project_type');
            if (this.project_type_code_list.length > 0){
                this.selected_project_type = this.project_type_code_list[0].codeId;
                this.onSearch();
            }
        },
        methods: {
            handleCurrentChange(val) //当前页修改
            {
                this.searchFormData.pageNum = val;
                this.search();
            },
            onReset() // 重置功能
            {
                this.$refs.searchFormRef.resetFields();
                this.search();
            },
            onSearch() // 查询按钮
            {
                this.searchFormData.pageNum = 1;
                this.search();
            },
            async search() /*通过项目类型查询*/ {
                this.searchFormData['project_type_code_id'] = this.selected_project_type;
                let data = await this.$search(this.searchFormData, 'OAMP_search_project_list');
                this.searchFormData.pageCount = data.pageCount;
                this.project_data = data.data;
            },
            tab_click(tab, event) {
                this.selected_project_type = this.project_type_code_list[tab.index].codeId;
                this.searchFormData.pageNum=1;
                this.search();
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .el-col {
        padding: 12px;
    }

    .el-tabs--border-card {
        height: 820px;
    }

    .project-container {
    }

    .project-content {
        height: 150px;
        background: #cccccc;
        text-align: center;
        border-radius: 15px;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
    }

    .project-info {
        padding-top: 15px;
        cursor: pointer;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
    }

    .set-ico {
        padding-top: 26px;
        font-size: 48px;
    }
</style>



